* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  height: 100%;
  background-color: #eaeaea;
  color: #252a34;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1 {
  font-size: 3rem;
  font-weight: 50;
  text-align: center;
  margin: 1rem 0 3rem;
  font-family: 'Space Mono', monospace;
}

.Inputdiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.input {
  padding: 0.5rem 1rem;
  outline: none;
  border: none;
  height: 50px;
  border-radius: 25px;
  width: 350px;
  margin: 0.25rem;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  font-size: 1.15rem;
}

.add {
  outline: none;
  border: none;
  height: 50px;
  border-radius: 25px;
  width: 50px;
  background-color: lime;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  margin: 0.25rem;
}

.add:hover {
  opacity: 0.7;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  margin-top: 2rem;
}

.item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0.5rem;
  border-bottom: 4px solid #fff;
  margin-bottom: 1.5rem;
}

.item_input {
  background: none;
  outline: none;
  border: none;
  padding: 5px;
  font-size: 1.4rem;
  width: 245px;
}

.edit {
  background: none;
  outline: none;
  border: none;
  color: lime;
  font-size: 1.4rem;
  font-family: 'Space Mono', monospace;
  margin: 0 0.5rem;
  cursor: pointer;
}

.editing {
  border-radius: 7px;
  /* padding: 5px; */
  background: #dddf53;
  /* box-shadow: 0px 0px 0px #bcbe47; */
}

.remove {
  background: none;
  outline: none;
  border: none;
  color: #e00;
  font-size: 1.4rem;
  font-family: 'Space Mono', monospace;
  cursor: pointer;
}

.form-group input {
  font-family: 'Baloo Bhai', cursive;
  text-align: center;
  display: block;
  width: 508px;
  padding: 1px;
  border: 2px solid black;
  margin: 11px auto;
  font-size: 25px;
  border-radius: 8px;
}

.form .btn {
  display: block;
  width: 20rem;
  margin: 20px auto;
}

.form .btn:hover {
  background-color: rgba(59, 59, 59, 0.411);
  color: black(59, 59, 59, 0.411);
}

.btn {
  font-family: 'Baloo Bhai', cursive;
  margin: 0px 9px;
  background-color: black;
  color: white;
  padding: 4px 14px;
  border: 2px solid grey;
  border-radius: 10px;
  font-size: 20px;
  cursor: pointer;
}

.textbox {
  font-family: Arial, Helvetica, sans-serif;
  background: rgba(255, 255, 255, 0.44);
  color: #333;
  border: 1px solid #a4a4a4;
  padding: 4px 8px 4px 4px !important;
  line-height: 1;
  width: 275px;
  /* height: 25px; */
}

.textbox:hover {
  border: 1px solid #ff00ff;
  -webkit-box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.3);
}

.textbox:focus {
  border: 1px solid #4d90fe;
  outline: none;
  -webkit-box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.3);

  background: rgb(255, 255, 255);
}

.likes,
.like,
.dislike,
.share,
.share,
.dislikes,
.like_dislike {
  display: inline-block;
  cursor: default;
}

/* .like,. */

.likes_and_share {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.fa-thumbs-up,
.fa-thumbs-down,
.fa-share {
  margin: 5px;
  cursor: pointer;
}



